import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Dog from '../../components/dog';
import DogAddLog from '../../components/dog/addlog';
import Teams from '../../components/team/teams';

const initialState = {
	isLoading: false,
};

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadDogs = async () => {
		const { showAllDogs } = this.state;
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;
		const query = {
			team: currentTeam,
		};
		if (showAllDogs) {
			query.deceased = 1;
		}
		await dogStore.load({ query, addData: ['team', 'user', 'workoutSummary', 'vaccineStatuses', 'historyStatuses', 'vaccineLogStatuses', 'allDogs'] });
		this.setState({ isLoading: false });
	}

	changeSort = (e) => {
		const value = e.target.value;
		this.setState({
			sortby: value,
		});
	}

	sortByFn = (a, b) => {
		let { sortby } = this.state;
		let reverse;

		if (sortby.match(/^-/)) {
			reverse = true;
			sortby = sortby.replace(/^-/, '');
		}

		if (reverse) {
			if (a[sortby] > b[sortby]) {
				return -1;
			}
			if (a[sortby] < b[sortby]) {
				return 1;
			}
		} else {
			if (a[sortby] < b[sortby]) {
				return -1;
			}
			if (a[sortby] > b[sortby]) {
				return 1;
			}
		}
		return 0;
	}

	toggleShowAllDogs = () => {
		const { showAllDogs } = this.state;
		this.setState({
			showAllDogs: !showAllDogs,
		}, () => this.loadDogs());
	}

	componentDidMount() {
		this.loadDogs();
	}

	render() {
		const { scrolledDown } = this.props;
		const { isLoading, sortby, showAllDogs } = this.state;
		const { dogStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const { dogs, vaccineStatuses, historyStatuses } = dogStore;

		const sortedDogs = sortby ? dogs.sort(this.sortByFn) : dogs;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top pt-2' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> <Text id='dogs.dashboard'>Oversikt</Text></a></nobr>
					</div>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<nobr><a href='/dogs/list' class='text-primary stretched-link'><i class='fas fa-list' /> <Text id='dogs.list'>Liste</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/feed' class='text-secondary stretched-link'><i class='fas fa-rss' /> <Text id='dogs.feed'>Feed</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/todo' class='text-secondary stretched-link'><i class='fas fa-calendar-check' /> <Text id='dogs.todo'>Todo</Text></a></nobr>
					</div>
				</div>

				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-2 border-bottom'>
							<Teams stores={this.props.stores} callback={this.loadDogs} />
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-2 pb-0 clearfix'>
							<span class='float-right'>
								<div class='form-row align-items-center'>
									<div class='col-auto my-1'>
										<select class='custom-select'
											style='background-color: inherit; border: none; padding-top: 0; padding-bottom: 0; height: 1.4em; font-size: 0.8em;'
											onInput={this.changeSort}
										>
											<option value='' selected={!sortby}>-- <Text id='dogs.choose-sort'>Choose sort</Text> ---</option>
											<option value='-distanceKm' selected={sortby === '-distanceKm'}><Text id='dogs.sort-distance'>Distance</Text> v</option>
											<option value='distanceKm' selected={sortby === 'distanceKm'}><Text id='dogs.sort-distance'>Distance</Text> ^</option>
											<option value='-duration' selected={sortby === '-duration'}><Text id='dogs.sort-duration'>Duration</Text> v</option>
											<option value='duration' selected={sortby === 'duration'}><Text id='dogs.sort-duration'>Duration</Text> ^</option>
											<option value='name' selected={sortby === 'name'}><Text id='dogs.sort-name'>Name</Text> v</option>
											<option value='-name' selected={sortby === '-name'}><Text id='dogs.sort-name'>Name</Text> ^</option>
											<option value='shortname' selected={sortby === 'shortname'}><Text id='dogs.sort-shortname'>Shortname</Text> v</option>
											<option value='-shortname' selected={sortby === '-shortname'}><Text id='dogs.sort-shortname'>Shortname</Text> ^</option>
											<option value='-count' selected={sortby === '-count'}><Text id='dogs.sort-workouts'>Workouts</Text> v</option>
											<option value='count' selected={sortby === 'count'}><Text id='dogs.sort-workouts'>Workouts</Text> ^</option>

											<option value='-posLead' selected={sortby === '-posLead'}><Text id='dogs.sort-lead'>Lead</Text> v</option>
											<option value='posLead' selected={sortby === 'posLead'}><Text id='dogs.sort-lead'>Lead</Text> ^</option>
											<option value='-posPoint' selected={sortby === '-posPoint'}><Text id='dogs.sort-point'>Point</Text> v</option>
											<option value='posPoint' selected={sortby === 'posPoint'}><Text id='dogs.sort-point'>Point</Text> ^</option>
											<option value='-posTeam' selected={sortby === '-posTeam'}><Text id='dogs.sort-team'>Team</Text> v</option>
											<option value='posTeam' selected={sortby === 'posTeam'}><Text id='dogs.sort-team'>Team</Text> ^</option>
											<option value='-posWheel' selected={sortby === '-posWheel'}><Text id='dogs.sort-wheel'>Wheel</Text> v</option>
											<option value='posWheel' selected={sortby === 'posWheel'}><Text id='dogs.sort-wheel'>Wheel</Text> ^</option>
										</select>
									</div>
								</div>
							</span>
							<h5>
								<Text id='dogs.all-dogs' fields={{ total: dogs.length }}>Alle hundene - {dogs.length} stk</Text>
							</h5>
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 pb-2 clearfix'>
							<span class='float-right'>
								<a class='btn btn-sm btn-primary ml-2'
									href='/dogs/edit/new'
								>
									<i class='fas fa-plus' /> <Text id='dogs.add-dogs'>Legg til hunder</Text>
								</a>
							</span>
							<DogAddLog stores={this.props.stores} dog={{}} callback={this.loadDogs} />
						</div>
						{!isLoading && dogs && dogs.length === 0 && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-muted'>
								<div class='text-center mb-5'>
									<div class='display-1'>
										<i class='fas fa-dog' />
									</div>
									<h5>
										<Text id='dogs.no-dogs-registered'>Ingen hunder er registrert ennå.</Text>
									</h5>
								</div>
								<Text id='dogs.howto-add-1'>Slik kommer du i gang:</Text>
								<ol>
									<li><Text id='dogs.howto-add-2'>Trykk på knappen "Legg til hunder" rett nedenfor.</Text></li>
								</ol>
							</div>
						)}
						{sortedDogs && sortedDogs.map(dog => (
							<Dog stores={this.props.stores} dog={dog} />
						))}
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-2'>
						<span
							class='pointerCursor ml-2 font-weight-lighter float-right'
							onClick={this.toggleShowAllDogs}
						>
							{showAllDogs ? <>
								<Text id='dogs.hide-deceased'>Vis kun nåværende team</Text>
							</> : <>
								<Text id='dogs.absolutely-all'>Vis absolutt alle</Text>
							</>}
						</span>
					</div>
				</div>
			</div>
		);
	}
}

export default Dogs;
